<script>
import api from '@/api'
import { getToken } from '@/api/auth'

export default {
  inject: ['reload'],
  data () {
    return {
      self: this,
      columns: [
          {
            title: 'ID',
            key: 'id',
            align: 'center',
          },
          {
            title: '广告位名称',
            key: 'group',
            align: 'center',
            render: (h, params) => {
              const type = this.intactData.adtype,
                    isImg = type == 5;

              if(!isImg && params.row.img) {
                return h('div', [
                  h('img', {
                    domProps: { // 修改标签默认内容
                      innerHTML: ""
                    },
                    attrs: {
                      src: params.row.img
                    },
                    style: {
                      margin: '5px 5px 0 5px',
                      maxWidth: '50px'
                    },
                    on: {
                      click: () => {
                        this.previewImg(params);
                      }
                    }
                  }, 'View')
                ]);
              } else {
                const text = params.row.group;
                return h('div', {}, text);
              }
            }
          },
          {
            title: '广告描述',
            key: 'describe',
            align: 'center',
          },
          {
            title: '链接',
            key: 'url',
            align: 'center',
          },
          {
            title: '状态',
            key: 'status',
            align: 'center',
            render: (h, params) => {
              const status = params.row.status;
              const color = status == 1 ? 'success' : status == 2 ? 'info' : status == 3 ? 'warning' : 'error';
              const text = status == 1 ? '已发布' : status == 2 ? '已隐藏' : status == 3 ? '审核中' : '已退回';

              return h('Tag', {
                props: {
                  type: 'dot',
                  color: color,
                }
              }, text);
            }
          },
          {
              title: '备注',
              key: 'remark',
              align: 'center',
          },
          {
              title: '发表时间',
              key: 'addtime',
              align: 'center',
          },
          {
            title: '操作',
            key: 'action',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  domProps: { // 修改标签默认内容
                    innerHTML: "修改"
                  },
                  props: {
                    type: 'primary',
                    size: 'small',
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.gotoAdvertEdit(params)
                    }
                  }
                }, 'View'),
                h('Button', {
                  domProps: { // 修改标签默认内容
                    innerHTML: "删除"
                  },
                  props: {
                      type: 'error',
                      size: 'small'
                  },
                  on: {
                      click: () => {
                        this.curAid = params.row.id
                        this.warnModal = true
                      }
                  }
                }, 'Delete')
              ]);
            }
          }
      ],
      data: [],
      isLoading: true,
      totalPages: 0,
      params: {},
      modal_loading: false,
      warnModal: false,
      intactData: {},
      toAdvertAddPath: '',
      openPreviewImg: false,
      curAid: ''
    }
  },
  mounted: function() {
    const token = getToken(),
          adid = this.$route.query.adid,
          params = {"token": token, "adid": adid, "page": "1", "per_page": "10", "need_menu":1};

    this.params = params;
    this.getData(params);
  },
  methods: {
    getData (params) {
      api.advertset(params).then(res => {
        if(res.code === 0) {
          this.data = res.data.article
          this.intactData = res.data.adinfo
          this.data.forEach(itm => {
            itm.group = this.intactData.group
            itm.addtime = this.$moment(itm.addtime*1000).format("YYYY-M-DD k:mm")
          })
          this.columns[1].title = this.intactData.adtype == 2 ? '图片' : '广告位名称';
          this.toAdvertAddPath = '/ad/advertadd?adid='+this.intactData.id+'&advtype='+this.intactData.adtype;
          this.totalPages = res.data.totlerows
          this.isLoading = false
        } else {
          this.$Notice.error({title: res.msg})
        }
        // console.log(res)
      }).catch(err => {
        this.$Notice.error({title: err})
      })
    },
    gotoAdvertEdit (params) {
      console.log(params)
      this.$router.push({
        path: '/ad/advertedit',
        query: {
          aid: params.row.id,
          advtype: this.intactData.adtype
        }
      })
    },
    changePage (page) {
      // console.log(page)
      this.params.page = page;
      this.getData(this.params);
    },
    changePageNum (num) {
      // console.log(num)
      this.params.per_page = num;
      this.getData(this.params);
    },
    advertsetDel () {
      console.log(this.curAid)
      
      this.modal_loading = true
      const token = getToken(),
            opea = 2,
            aid = this.curAid,
            adid = this.$route.query.adid,
            params = {"token": token, "opea": opea, "aid": aid, "adid": adid};

      api.advertopea(params).then(res => {
        console.log(res)
        this.warnModal = false
        this.modal_loading = false
        if(res.code == 0) {
          this.$Notice.success({
            "desc": res.msg
          })
          this.reload();
        } else {
          this.$Notice.error({
            "desc": res.msg
          })
        }
      }).catch(err => {
        this.warnModal = false
        this.modal_loading = false
        this.$Notice.error({
          "desc": err
        })
      })
    },
    previewImg (params) {
      const src = params.row.img
      console.log(params,src)
    }
  }
}
</script>
<template>
  <div>
    <h3><Icon type="ios-pin" size=24 />广告位：{{intactData.group || ''}}</h3>
    <div class="searchBar">
      <Button type="info" to="/ad/siteadd" ghost>新增广告位</Button>
      <Button type="success" :to="toAdvertAddPath" ghost>新增广告</Button>
      <Button type="warning" to="/ad/advert" ghost>广告位列表</Button>
    </div>
    <Table border :context="self" :columns="columns" :data="data" :loading=isLoading></Table>
    <div style="margin: 10px;overflow: hidden">
      <div style="float: right;">
        <Page :total="totalPages*1" show-total show-elevator show-sizer :page-size=10 @on-page-size-change="changePageNum" @on-change="changePage" />
      </div>
    </div>
    <Modal v-model="warnModal" width="400">
      <p slot="header" style="color:#f60;text-align:center">
        <Icon type="ios-information-circle" size=20></Icon>
        <span>删除确认</span>
      </p>
      <div style="text-align:center">
        <p>确认删除该内容么？</p>
      </div>
      <div slot="footer">
        <Button type="error" size="large" long :loading="modal_loading" @click="advertsetDel">Delete</Button>
      </div>
    </Modal>
  </div>
    
</template>
<style lang="scss" scoped>
  .ivu-btn-ghost {
    margin-right: 20px;
  }
  h3 {
    margin-bottom: 10px;
  }
  .searchBar {
    margin-bottom: 20px;
  }
</style>
